.profile__add {
  margin: 0 0 7px auto;
  padding: 0;
  min-width: 150px;
  min-height: 50px;
  border: 2px solid #fff;
  border-radius: 2px;
  background: transparent url("../../../images/add.svg") no-repeat center;
  transition: all 0.4s ease-in-out;
}

.profile__add:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media screen and (max-width: 644px) {
  .profile__add {
    min-width: 282px;
    background: transparent url("../../../images/add_small.svg");
    border: none;
    margin: 0 0 0 auto;
  }
}
